import { h } from '../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../node_modules/preact/dist/preact.mjs'

export default class DeleteModal extends Component {
  constructor() {
    super()
    this.state = { loading: false }
  }
  async delete() {
    this.setState({ loading: true })
    try {
      const res = await fetch(`/admin-data?${this.props.path}`, {
        method: 'POST',
      })
      const { error } = await res.json()
      if (error) this.setState({ error })
      else {
        this.props.onClose()
        this.props.onComplete()
      }
    } catch (error) {
      this.setState({ error })
    } finally {
      this.setState({ loading: false })
    }
  }
  render({ text, title, onClose, btnClass = 'primary', confirmText, closeText = '' }) {
    return h('div',{'className':"modal", 'tabIndex':"-1", 'role':"dialog", 'style':"display: block;"},
      h('div',{'className':"modal-dialog",'role':"document"},
        h('div',{'className':"modal-content"},
          h('div',{'className':"modal-header"},
            h('h5',{'className':"modal-title"}, title ),
            h('button',{'onClick':onClose,'type':"button",'className':"close",'data-dismiss':"modal",'aria-label':"Close"},
              h('span',{'aria-hidden':"true"},``),
            ),
          ),
          h('div',{'className':"modal-body"},
            h('p',{}, text ),
          ),
          h('div',{'className':"modal-footer"},
            h('button',{'onClick':onClose,'type':"button",'className':"btn btn-secondary",'data-dismiss':"modal"},closeText),
            h('button',{'disabled':this.state.loading,'type':"button",'className':`btn btn-${btnClass}`,'onClick':() => this.delete()},this.state.loading ? '...' : confirmText),
          ),
        ),
      ),
    )
  }
}

