import { h } from '../../node_modules/preact/dist/preact.mjs'
import { popup } from './Components/index.jsx'

const ArticleEditor = ({ article, onSave, name }) => {
  return h('div',{'className':"form-group"},
    h('label',{},``),
    h('div',{'dangerouslySetInnerHTML':{ __html: article },'className':"mb-3 ArticleHolder"}),
    h('a',{'onClick':(e) => {
      e.preventDefault()
      window['editorCallback'] = (html) => {
        editor.close()
        onSave(html)
      }
      window['editorGetData'] = () => article
      const editor = popup('/admin/editor', ' ', 900, 650)
      return false
    },'className':"btn btn-outline-success",'href':"#"},``),
    h('input',{'name':name,'type':"hidden",'value':article}),
  )
}

export default ArticleEditor