import { h } from '../../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../../node_modules/preact/dist/preact.mjs'

/**
 * The Bootstrap row.
 */
export const Row = ({ children, className, ...props }) => {
  const cl = `row${className ? ` ${className}` : ''}`
  return     h('div',{...props,'className':cl},children)
}

export const GrowingSpinner = ({ color = 'danger' }) => {
  return (h('div',{'className':`spinner-grow text-${color}`, 'role':"status"},
    h('span',{'className':"sr-only"},`Loading...`),
  ))
}

/**
 * The Bootstrap column.
 */
export const Col = ({ children, className, ...props }) => {
  const cl = `col${className ? ` ${className}` : ''}`
  return    h('div',{...props, 'className':cl},children)
}
export const A = ({ onClick, className, ...props }) => {
  return (h('a',{...props,'className':className, 'href':"#", 'onClick':(e) => {
    e.preventDefault()
    onClick(e)
    return false
  }}))
}

const Input = ({ textarea, required, name, placeholder, id, hid, type, file, value }) => {
  const props = { 'required': required, 'name': name, 'placeholder': placeholder, 'className': `form-control${file ? '-file' : ''}`, 'id': id, 'aria-describedby': hid }
  const input = textarea ?          h('textarea',{...props,'rows':typeof textarea == 'number' ? textarea : 3},value) : h('input',{...props,...(value ? { 'value': value } : {}),'type':type})
  return input
}

export const FormRow = ({
  label, type = 'text', placeholder, name, help, textarea,
  required, file, value, options, selectedOption,
}) => {
  const id = `i${Math.random() * 100000}`
  const hid = `h${id}`

  const commonProps = {
    hid, id, value, name, required,
  }
  const I = options ?    h(Select,{...commonProps,options:options, selectedOption:selectedOption }) :      h(Input,{...commonProps,textarea:textarea, placeholder:placeholder, type:type, file:file})

  return h('div',{'className':"form-group"},
    h('label',{'htmlFor':id},label),
    I,
    help && h('small',{'id':hid,'dangerouslySetInnerHTML':{ __html: help },'className':"form-text text-muted"}),
  )
}

const Select = ({ options, name, value, required, id, hid }) => {
  return       h('select',{'name':name, 'value':value, 'required':required, 'className':"custom-select", 'id':id, 'aria-describedby':hid},
    h('option'),
    options.map(({ value: v, title }) => {
      return h('option',{'key':v,'value':v,'selected':v==value},
        title,
      )
    }),
  )
}

export const Icon = ({ icon }) =>
  h('span',{},h('i',{'className':icon}),` `)

export class Switch extends Component {
  shouldComponentUpdate(_, __, newContext) {
    const { name } = this.props
    return this.context.values[name] != newContext.values[name]
  }
  componentDidMount() {
    const { value, name } = this.props
    const { onChange } = this.context
    if (value !== undefined) onChange(name, value)
  }
  render({ name, label, value, required }) {
    const  { id, onChange, hid, values = {} } = this.context
    const rendered = name in values // for SSR
    return (                                                      h('div',{'className':"custom-control custom-switch"},
      h('input',{'required':required !== undefined,'name':name,'checked':rendered ? values[name] : value,'id':id,'type':"checkbox",'className':"custom-control-input",'aria-described-by':hid,'onChange':(e) => {
          onChange(name, e.currentTarget.checked)
        }}),
      h('label',{'htmlFor':id,'className':"custom-control-label"},label),
    ))
  }
}

export const ErrorAlert = ({ error }) => {
  if (!error) return null
  if (typeof error != 'string') error = ''
  return (h('div',{'className':"alert alert-danger mt-3", 'role':"alert"},error))
}

export const Success = ({ success, message }) => {
  if (!success) return null
  return (h('div',{'className':"alert alert-success mt-3", 'role':"alert"},message || success))
}