import { h } from '../../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../../node_modules/preact/dist/preact.mjs'
import fetch from '../../../node_modules/unfetch/dist/unfetch.mjs'
import { Col, Row, Icon } from './../../frontend/components/Bootstrap.jsx'
import DeleteModal from './../DeleteModal.jsx'

export default class Pages extends Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      data: [],
    }
  }
  async componentDidMount() {
    await this.load()
  }
  async load() {
    this.setState({ loading: true })
    try {
      const res = await fetch('/admin-data?pages')
      const { error, data } = await res.json()
      if (error) this.setState({ error })
      else this.setState({ data })
    } catch (error) {
      this.setState({ error })
    } finally {
      this.setState({ loading: false })
    }
  }
  render() {
    return h(Col,{},
      h('h1',{},` `),
      h('p',{},
        ` -    :`
      ),
      this.state.loading && h('span',{'className':"echo-loader"},`Loading`),
      this.state.data.map(({ _id, ...item }) => {
        return h(ItemRow,{...item,key:_id,id:_id,onDelete:() => this.load()})
      }),
    )
  }
}

class ItemRow extends Component {
  constructor() {
    super()
    this.state = {
      modal: null,
    }
  }
  render() {
    const { seo, id, description, onDelete, title } = this.props
    return h(Row,{className:"CategoryRow"},
      h(Col,{},
        h('h2',{},title),
        h('em',{},`knedv.ru/`,seo),
        h('p',{},description),
      ),
      h(Col,{className:"col-1 CategoryMeta"},
        h('a',{'href':`/admin/add-page/${id}`,'style':"color:brown;"},h(Icon,{icon:"fas fa-pen"})),
        h('br'),
        h('a',{'onClick':(e) => {
          e.preventDefault()
          this.setState({
            modal: {
              text: h('span',{},`     `,h('strong',{},title),`?`),
              confirmText: '',
              title: ' ',
              path: `pages&id=${id}&delete`,
            },
          })
          return false
        },'style':"color:brown;",'href':"#"},h(Icon,{icon:"far fa-trash-alt"})),
      ),
      this.state.modal &&
        h(DeleteModal,{...this.state.modal,onClose:() => {
          this.setState({ modal: null })
        },btnClass:"danger",onComplete:onDelete})
      ,
    )
  }
}