import { h } from '../../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../../node_modules/preact/dist/preact.mjs'
import { Col, Row, Icon } from './../../frontend/components/Bootstrap.jsx'
import DeleteModal, { EditModal } from './../DeleteModal.jsx'
import ObjectForm from './Forms/Object.jsx'
import { loadData } from './../Components/LoadData.jsx'

export default class Objects extends Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      data: [],
      modal: null,
      edit: null,
    }
  }
  async componentDidMount() {
    await this.load()
  }
  async load() {
    /** @type {Array<Property>}  */
    const data = await loadData.bind(this)('objects')
    if (data) this.setState({ data })
  }
  openModal(modal) {
    this.setState({ modal })
  }
  openEdit(edit) {
    this.setState({ edit })
  }
  render() {
    return                                                        h(Col,{},
      h('h1',{},` `),
      h('p',{},
        `    :`
      ),
      h(List,{data:this.state.data,loading:this.state.loading,openModal:this.openModal.bind(this),openEdit:this.openEdit.bind(this)}),
      this.state.modal &&
        h(DeleteModal,{...this.state.modal,onClose:this.openModal.bind(this, null),btnClass:"danger",onComplete:this.load.bind(this)})
      ,
      this.state.edit &&
        h(EditModal,{onClose:this.openEdit.bind(this, null),title:" "},
          h(ObjectForm,{id:this.state.edit._id,submitFinish:this.load.bind(this),onClose:this.openEdit.bind(this, null),path:"/admin-data?objects",closeText:"",successMessage:"  !",confirmText:""}),
        )
      ,
    )
  }
}

const List = ({ data, openModal, openEdit, loading }) => {
  return (                                      h('div',{},
    loading && h('span',{'className':"echo-loader"},`Loading`),
    !loading && !data.length && '  .',
    data.map((item, i) => {
      const even = i % 2 == 0
      return h(Item,{item:item,key:item._id,openModal:openModal,openEdit:openEdit,style:`background:${!even ? '#edeee8' : 'white'};`})
    }),
  ))
}


class Item extends Component {
  render({ item, openModal, openEdit, style }) {
    const { title, image, description, seo, _id, categorySeo, price, numberOfPhotos } = item
    // read host ???
    const s = `//${categorySeo}/${seo}`
    const link = `knedv.ru${s}`
    return h(Row,{ style:style,className:"CategoryRow"},
      h(Col,{className:"col-3 col-sm-4 "},h('img',{'src':image,'className':"img-fluid p-1"}),
        price && `: ${price}`,
      ),
      h(Col,{},
        h('h2',{},title),
        h('em',{},h('a',{'href':s},link)),
        h('p',{},description),
      ),
      h(Col,{className:"col-1 CategoryMeta"},
        h('br'),
        h('a',{'onClick':(e) => {
          e.preventDefault()
          openModal({
            text: h('span',{},`     `,h('strong',{},title),`?`),
            confirmText: '',
            title: ' ',
            path: `objects&id=${_id}&delete`,
          })
          return false
        },'style':"color:brown;",'href':"#"},h(Icon,{icon:"far fa-trash-alt"})),
        h('a',{'onClick':(e) => {
          e.preventDefault()
          openEdit(item)
          return e
        },'style':"color:brown;",'href':"#"},
          h(Icon,{icon:"fas fa-pen"}),
        ),
        h('a',{'href':`/admin/albums/${_id}`,'style':"color:brown;",'onClick':(e) => {
          e.preventDefault()
          openEdit(item)
          return e
        }},
          h(Icon,{icon:"fas fa-images"}),
          numberOfPhotos ? `(${numberOfPhotos})` : '',
        ),
      ),
    )
  }
}

