import { h } from '../../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../../node_modules/preact/dist/preact.mjs'
import fetch from '../../../node_modules/unfetch/dist/unfetch.mjs'
import { Col, Icon } from './../../frontend/components/Bootstrap.jsx'
import DeleteModal, { EditModal } from './../DeleteModal.jsx'
import SpecialsForm from './Forms/Specials.jsx'
import { loadData } from './../Components/LoadData.jsx';

export default class Special extends Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      data: [],
    }
  }
  async componentDidMount() {
    await this.load()
  }
  async load() {
    /** @type {Array<Special>} */
    const data = await loadData.bind(this)('specials')
    if (data) this.setState({ data })
  }
  openModal(modal) {
    this.setState({ modal })
  }
  openEdit(edit) {
    this.setState({ edit })
  }
  render() {
    const f =      h('details',{},
      h('summary',{},
        h('h3',{'style':"display: inline-block;vertical-align: middle;"},`  `),
      ),
      h(SpecialsForm,{submitFinish:() => {
        this.load()
      },path:"/admin-data?specials",successMessage:"  !",confirmText:""}),
    )
    return                                                                  h(Col,{},
      h('h1',{},` `),
      h(List,{data:this.state.data,loading:this.state.loading,openModal:this.openModal.bind(this),openEdit:this.openEdit.bind(this)}),
      h('hr'),
      f,
      this.state.modal &&
        h(DeleteModal,{...this.state.modal,onClose:this.openModal.bind(this, null),btnClass:"danger",onComplete:this.load.bind(this)})
      ,
      this.state.edit &&
        h(EditModal,{onClose:this.openEdit.bind(this, null),title:""},
          h(SpecialsForm,{item:this.state.edit,submitFinish:this.load.bind(this),onClose:this.openEdit.bind(this, null),path:"/admin-data?specials",closeText:"",successMessage:"  !",confirmText:""}),
        )
      ,
    )
  }
}

const List = ({ data, openModal, openEdit, loading }) => {
  return (                           h('div',{'style':"height:25rem;overflow:scroll;background:wheat; padding:0.5rem;"},
    loading && h('span',{'className':"echo-loader"},`Loading`),
    !loading && !data.length && '  .',
    data.map((item) => {
      return h(Item,{item:item,key:item._id,openModal:openModal,openEdit:openEdit})
    }),
  ))
}


const Item = ({ item, openModal, openEdit }) => {
  const { _id, title, cdnImage, description, price, show_on_main } = item
  const shownOnMain = show_on_main == 'on'
  return (h('div',{ 'className':shownOnMain ? 'IsShownOnMain' : '','style':"border-bottom:1px solid brown;border-top:1px solid lightcoral;"},
    h('h4',{},title,` `,shownOnMain && h('span',{'className':"badge badge-danger"},` `)),
    h('p',{},
      h('img',{'src':cdnImage,'style':"display:block;"}),
      description,
      h('span',{'style':"font-weight: bold;"},` `,price),
    ),
    h('a',{'onClick':(e) => {
      e.preventDefault()
      openModal({
        text: h('span',{},`     `,h('strong',{},title),`?`),
        confirmText: '',
        title: ' ',
        path: `specials&id=${_id}&delete`,
      })
      return false
    },'style':"color:brown;",'href':"#"},
      h(Icon,{icon:"far fa-trash-alt"}),
    ),
    h('a',{'onClick':(e) => {
      e.preventDefault()
      openEdit(item)
      return e
    },'style':"color:brown;",'href':"#"},
      h(Icon,{icon:"fas fa-pen"}),
    ),
  ))
}