import { h } from '../../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../../node_modules/preact/dist/preact.mjs'
import { Col, Row, ErrorAlert, Success, GrowingSpinner } from './../../frontend/components/Bootstrap.jsx'
import { loadData } from './../Components/LoadData.jsx'
import { LoadingIndicator } from './../Components/index.jsx'
// import 'preact/devtools/'
import PhotoUploader from './../Components/PhotoUploader/index.jsx'
import Form, { FormGroup, SubmitButton, SubmitForm } from '../../../node_modules/@depack/form/build/index.js'

/**
 * The Gallery with the photo display at the top and upload form at the bottom. When a photo is uploaded via the inner photo uploaded, the `load` method is triggered which refreshes the list.
 */
export default class Gallery2 extends Component {
  constructor() {
    super()
    this.state = { data: null, loading: true, files: [], uploadedResults: [] }
  }
  async componentDidMount() {
    await this.load()
    this.setState({ preventLoader: true }) // no more page loaders
  }
  /**
   * Loads the galleries list from the server.
   * Side-effects: sets the `error`, `data` and `loading` on the state.
   */
  async load() {
    const { id, server = 'galleries' } = this.props
    if (!id) this.setState({ loading: false, error: 'No id' })
    const data = await loadData.bind(this)(`${server}&id=${id}`)
    /** @type {Gallery} */
    if (data) this.setState({ data })
  }
  get data() {
    return this.state.data
  }
  addUploadedResults(results) {
    this.setState({ uploadedResults:
      [...this.state.uploadedResults, ...results],
    })
  }
  render() {
    const { pageTitle = '' } = this.props
    const { title, cdnImage, description, _id, photos } = this.data || {}
    const { uploadedResults, loading, preventLoader } = this.state
    return (                                      h(Col,{},
      h('h1',{},pageTitle),

      !preventLoader && loading && h(LoadingIndicator),
      this.data && h(Row,{className:"mb-3"},
        h(Col,{className:"col-sm-3"},
          h('img',{'src':cdnImage,'className':"img-fluid"}),
        ),
        h(Col,{},
          h('h2',{},title),
          description,
        ),
      )
      ,
      this.data && h(PhotoList,{photos:photos,loading:loading}),
      h('hr'),
      _id && h(GalleryForm,{uploadedResults:uploadedResults,submitFinish:async (result) => {
          // the form responds with ids of added uploads
          const { 'data': res } = await result.json()
          if (res) {
            this.addUploadedResults(res)
            await this.load()
          }
        },galleryId:_id,path:"/admin-data?photos",confirmText:" "}),
    ))
  }
}

