class PhotoList extends Component {
  render({ photos, loading }) {
    return (h(Row,{},
      photos.map(({ file, _id: i }) => {
        return (h(Col,{key:i,className:"col-sm-4",style:"padding:.25rem;"},
          h('img',{'src':file,'className':"img-fluid",'style':"max-height: 200px;"})))
      }),
      loading && h(Col,{className:"col-sm-4"},
        h('div',{'className':"h-100 w-100 d-flex align-items-center rounded PhotoLoadingPlaceholder"},
          h('span',{'className':"align-middle"},
            ` ... `,h('br'),
            h(GrowingSpinner),
          ),
        ),
      ),
    ))
  }
}

/**
 * This is the form to upload pictures.
 */
class GalleryForm extends SubmitForm {
  addLinks() {
    if (this.photoUploader) {
      this.photoUploader.externalAPI()
    }
  }
  render({ galleryId, confirmText, uploadedResults }) {
    const { formLoading, error, success } = this.state
    return (
                                        h(Form,{onSubmit:this.submit.bind(this)},
        h('input',{'value':galleryId,'name':"galleryId",'type':"hidden"}),
        h(FormGroup,{label:" ",help:"     ."},
          h(PhotoUploader,{ref:(r) => {
            this.photoUploader = r
          },onPhotoUploaded:async () => {
            this.reset()
            // await new Promise(r => setTimeout(r, 1))
            // this.submit({ target: this.base, preventDefault() {} })
          },onAdded:() => this.reset(),onRemove:() => this.reset(),uploadedResults:uploadedResults}),
        ),
        h(SubmitButton,{loading:formLoading,confirmText:confirmText,loadingText:"..."}),
        h(ErrorAlert,{error:error}),
        h(Success,{success:success,message:" !"}),
      ))
  }
}

const Details = ({ title, children }) => {
  return (    h('details',{},
    h('summary',{},
      h('h3',{'style':"display: inline-block;vertical-align: middle;"},title),
    ),
    children,
  ))
}