import { h } from '../../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../../node_modules/preact/dist/preact.mjs'
import { Col, Row } from './../../frontend/components/Bootstrap.jsx'
import { loadData } from './../Components/LoadData.jsx'
import { LoadingIndicator } from './../Components/index.jsx'
import GalleryForm from './Forms/Gallery.jsx'

export default class Gallery extends Component {
  constructor() {
    super()
    this.state = { data: [], loading: true }
  }
  async componentDidMount() {
    await this.load()
  }
  /**
   * Loads the galleries list from the server.
   * Side-effects: sets the `error`, `data` and `loading` on the state.
   */
  async load() {
    /** @type {Array<Gallery>} */
    const data = await loadData.bind(this)('galleries')
    if (data) this.setState({ data })
  }
  get data() {
    return this.state.data
  }
  render() {
    const { loading } = this.state
    return (    h(Col,{},
      h('h1',{},``),

      loading && h(LoadingIndicator),
      !loading && !this.state.data.length && '  .',
      this.state.data.map(({ _id, title, cdnImage, description }) => {
        return (h(Row,{key:_id},
          h(Col,{className:"col-sm-3"},
            h('img',{'src':cdnImage,'className':"img-fluid"}),
          ),
          h(Col,{},
            h('h2',{},title),
            h('a',{'href':`/admin/galleries/${_id}`},``),
            description,
          ),
        ))
      }),
      h('hr'),
      h(Details,{title:"  "},
        h(GalleryForm,{submitFinish:() => {
          this.load()
        },path:"/admin-data?galleries",successMessage:"  !",confirmText:""}),
      ),
    ))
  }
}

const Details = ({ title, children }) => {
  return (    h('details',{},
    h('summary',{},
      h('h3',{'style':"display: inline-block;vertical-align: middle;"},title),
    ),
    children,
  ))
}