import { h } from '../../../../node_modules/preact/dist/preact.mjs'
import Form, {
  FormGroup, Input, TextArea, SubmitForm, SubmitButton,
} from '../../../../node_modules/@depack/form/build/index.js'
import { Success, ErrorAlert } from './../../../frontend/components/Bootstrap.jsx'
import FormImage from './../../Components/FormImage.jsx'

class GalleryForm extends SubmitForm {
  render({ item, onClose, closeText = '', successMessage, confirmText = '' }) {
    const i = item || {}
    const { formLoading, error, success } = this.state
    return (             h(Form,{onSubmit:this.submit.bind(this), onChange:() => {
      this.reset()
    }},
      h(FormGroup,{label:"",help:"      ."},
        h(Input,{value:i.title,placeholder:",  706",name:"title",required:true}),
      ),
      h(FormGroup,{label:"",help:"  ..."},
        h(TextArea,{name:"description",required:true,placeholder:" "},i.description),
      ),

      h(FormImage,{editing:item,help:"  .",required:true,image:i.cdnImage}),

      item && h('input',{'value':i._id,'type':"hidden",'name':"id"}),

      h(ErrorAlert,{error:error}),
      h(Success,{success:success,message:successMessage}),

      h(SubmitButton,{confirmText:confirmText,loading:formLoading,loadingText:"..."}),

      onClose &&
        h('button',{'onClick':onClose,'type':"button",'className':"FormCancelBtn btn btn-secondary"},closeText)
      ,
    ))
  }
}

export default GalleryForm